<template>
  <div style="margin-left: 20%">
      <Table :columns="columns" :data="data" style="width: 1000px"></Table>
  </div>
</template>

<script>
    import {getUserFind} from '../api/index'
export default {
  name: 'HelloWorld',
  data () {
    return {
      data: [
          // {"id":1,"name":"夏言","birthday":-12527251200000,"address":"桂州村"},
          // {"id":2,"name":"严嵩","birthday":-12086352000000,"address":"分宜县城介桥村"},
          // {"id":3,"name":"徐阶","birthday":-12306412800000,"address":"明松江府华亭县"},
          // {"id":4,"name":"高拱","birthday":-12748176000000,"address":"河南省新郑市高老庄村"},
          // {"id":5,"name":"张居正","birthday":-13000636800000,"address":"江陵"}
      ],
        columns: [
            {
                title: '姓名',
                key: 'name'
            },
            {
                title: '生日',
                key: 'birthday'
            },
            {
                title: '籍贯',
                key: 'address'
            }
        ]
    }
  },
    created() {
      this.getUserFind();
    },
    methods: {
        getUserFind() {
            getUserFind().then((res) => {
                console.log(res);
                this.data = res;
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



